<template>

  <div id="mine">
    <my-choose :data="tab_data" @choose="choose"></my-choose>
    <div class="mine_content">
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
import myChoose from '../../../../components/myChoose'

export default {
  name: 'mine',
  components: {
    myChoose
  },
  data () {
    return {
      tab_data: [
        {
          name: '我的问答',
          path: 'PA'
        }, {
          name: '我的收藏',
          path: 'Collect'
        }, {
          name: '我的关注',
          path: 'Attended'
        }
      ]
    }
  },
  methods: {
    choose () {
    }
  }
}
</script>

<style scoped>
  #mine {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: #F6F7F8;
  }

  #mine .mine_content {
    position: absolute;
    width: 100%;
    left: 0;
    bottom: 0;
    top: 4rem;
  }
</style>



