<template>
  <div id="myChoose">
    <div class="myChoose_tabLine" ref="myChoose_tabLine">
      <div class="myChoose_tab" ref="myChoose_tab" v-for="(item,index) in data" :key="item.path" @click="choose(index)">
        {{item.name}}
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'myChoose',
  props: {
    data: Array
  },
  mounted () {
    this.init()
  },
  methods: {
    init () {
      let myChooseTabLine = this.$refs.myChoose_tabLine
      let myChooseTab = this.$refs.myChoose_tab
      let l = myChooseTabLine.clientWidth / this.data.length - 1
      // 不可用toFixed 因为会四舍五入导致子项相加大于父项，会导致溢出或换行 -1是因为为div设置了0.5px border
      l = parseInt(l * 100) / 100 - 1 + 'px'
      for (let i of myChooseTab) {
        i.style.width = l
      }
      this.choose(0)
    },
    choose (index) {
      let myChooseTab = this.$refs.myChoose_tab
      for (let i of myChooseTab) {
        i.className = 'myChoose_tab'
      }
      myChooseTab[index].className += ' active'
      this.$router.push({
        name: this.data[index].path
      })
    }
  }
}
</script>

<style scoped>
  #myChoose {
    height: 2rem;
    padding: 1rem;
  }

  #myChoose .myChoose_tabLine {
    height: 2rem;
    background-color: rgba(242, 242, 242, 1);
    border-radius: 2px;
  }

  #myChoose .myChoose_tabLine .myChoose_tab {
    display: inline-block;
    text-align: center;
    line-height: 2rem;
    font-size: 0.97rem;
    color: #E73E3A;
    border: 0.5px solid #E73E3A;
  }

  #myChoose .myChoose_tabLine .active {
    background-color: #E73E3A;
    color: #FFFFFF;
  }
</style>



